import React from 'react';
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <div name='footer' className='w-full h-fit bg-gray-200 text-black flex items-center border-t border-gray-400'>
        <div className={'w-full max-w-6xl py-8 mx-auto grid grid-cols-1 md:grid-cols-4 gap-4'}>
            <div className='flex flex-col h-full w-full items-center md:justify-start justify-center'>
                <Link to='/' className="text-2xl bg-purple-500 py-1 px-3 text-white tracking-widest font-bold">InterEd</Link>
            </div>
            <div className='flex flex-col h-full w-full items-center md:justify-start justify-center'>
                <h1 className='text-md font-bold tracking-wider text-gray-500 mb-4'>COMPANY</h1>
                <Link to='/about' className='text-lg hover:underline'>About</Link>
            </div>
            <div className='flex flex-col h-full w-full items-center md:justify-start justify-center'>
                <h1 className='text-md font-bold tracking-wider text-gray-500 mb-4'>COMMUNITIES</h1>
                <p className='text-lg'>Community1</p>
                <p className='text-lg'>Community2</p>
            </div>
            <div className='flex flex-col h-full w-full items-center md:justify-start justify-center'>
                <h1 className='text-md font-bold tracking-wider text-gray-500 mb-4'>USEFUL LINKS</h1>
                <p className='text-lg'>Support</p>
            </div>
        </div>
    </div>
  );
};


export { Footer as default}